@import '../../../../partials/slide/slide';
.slide-three {
    @include slide;
    &__tile-title{
        &--naut{
            background-image: linear-gradient(45deg, rgb(80, 145, 189), rgb(76, 198, 255),rgb(132, 193, 251), rgb(187, 189, 190) );
        }
        &--muse{
            background-image: linear-gradient(45deg, rgb(182, 66, 182), rgb(241, 111, 241),rgb(253, 224, 162), rgb(247, 196, 131) );
        }
    }
    &__tile-pic {
        &--naut{
            height: 24vh;
        }
    }
    &__tile-title{
        font-size: 5vw;
    }
}