@mixin buttonAnimate {
    animation: fadeIn cubic-bezier(1,-0.17,1,0) 3s;
    -webkit-animation: fadeIn cubic-bezier(1,-0.17,1,0) 3s;
    -moz-animation: fadeIn cubic-bezier(1,-0.17,1,0) 3s;
    -o-animation: fadeIn cubic-bezier(1,-0.17,1,0) 3s;
    -ms-animation: fadeIn cubic-bezier(1,-0.17,1,0) 3s;
    @keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:1;}
    }
    @-moz-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:1;}
    }
    @-webkit-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:1;}
    }
    @-o-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:1;}
    }
    @-ms-keyframes fadeIn {
        0% {opacity:0;}
        100% {opacity:1;}
    }
}